import React from "react";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import axios from "axios";




export const Contact =()=>{
    
    
    let history = useNavigate();
    const [data, setData] = useState({
        full_name: "",
        email_id: "",
       meg_all: ""
    })

    const handleChange = (e) => {
        setData({ ...data, [e.target.name]: e.target.value });
    
    }
   
    const submitForm = (e) => {
        e.preventDefault();
        const send_data = {
            full_name: data.full_name,
            email_id: data.email_id,
            meg_all: data.meg_all

        };
        axios.post('http://localhost/react-js-php/conatct.php',send_data)
        .then((results) => {
            if (results.data.Status === 'Invalid') {
                alert('Invalid User');
            }

            else {
                
                history(`/contact`);
            }
        })
    } 
    return(
<div>
        
<hr />





<div className="container">
    <div className="row">
        <div className="col">
            <div className="card">
                <div className="card-header bg-primary text-white"><i className="fa fa-envelope"></i> Contact us.
                </div>
                <div className="card-body">
                    <form onSubmit={submitForm}>
                        <div className="form-group">
                            <label for="name">Name</label>
                            <input type="text" className="form-control" id="name" aria-describedby="emailHelp" placeholder="Enter name" name="full_name" onChange={handleChange} value={data.full_name} required    />
                        </div>
                        <div className="form-group">
                            <label for="email">Email address</label>
                            <input type="email" className="form-control" id="email" aria-describedby="emailHelp" placeholder="Enter email" name="email_id" onChange={handleChange} value={data.email_id}   required />
                            <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="form-group">
                            <label for="message">Message</label>
                            <textarea className="form-control" id="message" rows="6" name="meg_all" onChange={handleChange} value={data.meg_all}    required></textarea>
                        </div> <br />
                        <div className="text-center">
                        <button type="submit" className="btn btn-primary text-right">Submit</button></div>
                    </form>
                </div>
            </div>
        </div>
        <div className="col-12 col-sm-4">
            <div className="card bg-light mb-3">
                <div className="card-header bg-success text-white text-uppercase"><i className="fa fa-home"></i> Address</div>
                <div className="card-body">
                    <p>  Vardhaman Nagar</p>
                    <p>440016 Nagpur</p>
                    <p>India</p>
                    <p>Email : pmcollection@gmail.com</p>
                    <p>Tel. +91 88888-00000</p>

                </div>

            </div>
        </div>
    </div>
</div>






















<hr />

</div>

        

       
   
 
    )
}