import React from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { useState } from "react";
import axios from 'axios';

const Register =(props)=>{

    

    let history = useNavigate();
    const [data, setData] = useState({
        full_name: "",
        email_id: "",
        mob_num: "",
        password: "",
        re_password: ""
    })

    const handleChange = (e) => {
        setData({ ...data, [e.target.name]: e.target.value });

        //console.log(data); 
    }
    const submitForm = (e) => {
        e.preventDefault();
        const send_data = {
            full_name: data.full_name,
            email_id: data.email_id,
            mob_num: data.mob_num,
            password: data.password,
            re_password: data.password

        };
        

        axios.post('http://localhost/react-js-php/apiinsert.php',send_data)
            .then((results) => {
                if (results.data.Status === 'Invalid') {
                    alert('Invalid User');
                }

                else {
                    history(`/dashboard`);
                }
            })
    }




    return (
        <div className='container col-md-4'>
            <div className='card-body'>
            <form onSubmit={submitForm}>



                <div className="form-outline mb-4">
                    <label className="form-group" for="form2Example1">Name</label>
                    <input type="text" id="form2Example1" name='full_name' className="form-control" onChange={handleChange} value={data.full_name} />



                    <label className="form-label" for="form2Example2">Email address</label>
                    <input type="email" id="form2Example2" name='email_id' className="form-control" onChange={handleChange} value={data.email_id} />



                    <label className="number" for="form2Example3">Mobile Number</label>

                    <input type="number" id="form2Example3" name='mob_num' className="form-control" onChange={handleChange} value={data.mob_num} />


                    <label className="form-label" for="form2Example4">Password</label>
                    <input type="password" id="form2Example4" name='password' className="form-control" onChange={handleChange} value={data.password} />




                   <div className='mb-3'>
                    <label className="form-label" for="form2Example5">Re-Enter Password</label>
                    <input type="password" id="form2Example5" name='re_password' className="form-control" onChange={handleChange} value={data.re_password} />
                    </div>
                    <div class="mb-3 text-center">
                        <button type="submit" class="btn btn-primary">Register</button>
                    </div>
                </div>

                {/* <!-- Register buttons --> */}
                <div className="text-center">
                    <p>Have already account <Link to="/login">Login</Link></p>
                    <p>or sign up with:</p>
                    <button type="button" className="btn btn-link btn-floating mx-1">
                        <i className="fa fa-facebook-f"></i>
                    </button>

                    <button type="button" className="btn btn-link btn-floating mx-1">
                        <i className="fa fa-google"></i>
                    </button>

                    <button type="button" className="btn btn-link btn-floating mx-1">
                        <i className="fa fa-twitter"></i>
                    </button>

                    <button type="button" className="btn btn-link btn-floating mx-1">
                        <i className="fa fa-github"></i>
                    </button>
                </div>
            </form>







            
            </div>



            </div>

    )
}

export default Register