import React from 'react';
import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';


const Login=()=>{
  let navigate = useNavigate();
  const [user , setUser]=useState({email_id:'', password:''})

  const handleChange=(e)=>{
    setUser({...user, [e.target.name]: e.target.value });
  }

  const submitForm = (e) => {
    e.preventDefault();
    const sendData = {
        email_id: user.email_id,
       password: user.password
       };
    

    axios.post('http://localhost/react-js-php/login.php',sendData)
        .then((result) => {
            if (result.data.Status === '200') {
              window.localStorage.setItem('email_id', result.data.email_id);
              window.localStorage.setItem('full_name', result.data.full_name);
              navigate(`/dashboard`);
            }

            else {
              alert('Invalid User');
               
            }
        })
}








  return (
    <div className='container col-md-4' >

      <form  onSubmit={submitForm} >
        
        <div className="form-outline mb-4">
          <label className="form-label" for="form2Example1">Email address</label>
          <input type="email" id="form2Example1" className="form-control" onChange={handleChange} value={user.email_id} name="email_id" />

        </div>

        
        <div className="form-outline mb-4">
          <label className="form-label" for="form2Example2">Password</label>
          <input type="password" id="form2Example2" className="form-control" onChange={handleChange} value={user.password} name="password"   />

        </div>

        
        <div className="row mb-4">
          <div className="col d-flex justify-content-center">

            <div className="form-check">
              <label className="form-check-label" for="form2Example31"> Remember me </label>
              <input className="form-check-input" type="checkbox" value="" id="form2Example31" checked />

            </div>
          </div>

          <div className="col">
            
            <Link to="#">Forgot password?</Link>
          </div>
        </div>

      
        <div className='text-center'>
          <button type="button" className="btn btn-primary btn-block mb-4">Login</button>
        </div>
        
        <div className="text-center">
          <p>Not member? <Link to="/register">Register</Link></p>
          <p>or sign up with:</p>
          <button type="button" className="btn btn-link btn-floating mx-1">
            <i className="fa fa-facebook-f"></i>
          </button>

          <button type="button" className="btn btn-link btn-floating mx-1">
            <i className="fa fa-google"></i>
          </button>

          <button type="button" className="btn btn-link btn-floating mx-1">
            <i className="fa fa-twitter"></i>
          </button>

          <button type="button" className="btn btn-link btn-floating mx-1">
            <i className="fa fa-github"></i>
          </button>
        </div>
      </form>

    
  




      
    </div>








   

  )
}

export default Login;